header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  backdrop-filter: blur(20px);
  background: var(--bg-glass);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  transition: all var(--transition-normal);
}

header.scrolled {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(20px);
  box-shadow: var(--shadow-lg);
}

nav {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
}

.nav-logo {
  font-size: 1.5rem;
  font-weight: 700;
  font-family: var(--font-heading);
}

.nav-logo a {
  color: var(--text-primary);
  background: linear-gradient(
    135deg,
    var(--primary-color),
    var(--secondary-color)
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-decoration: none;
  transition: all var(--transition-normal);
}

.nav-logo a:hover {
  transform: scale(1.05);
}

.nav-links {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.nav-links a {
  color: var(--text-primary);
  font-weight: 500;
  font-size: 0.95rem;
  position: relative;
  transition: all var(--transition-normal);
  padding: 0.5rem 1rem;
  border-radius: var(--radius-md);
}

.nav-links a:hover {
  color: var(--primary-color);
  background: rgba(37, 99, 235, 0.1);
  transform: translateY(-1px);
}

.nav-links a::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0;
  height: 2px;
  background: var(--primary-color);
  transition: all var(--transition-normal);
  transform: translateX(-50%);
}

.nav-links a:hover::after {
  width: 80%;
}

.nav-links .btn {
  background: var(--bg-primary);
  color: var(--text-white);
  font-size: 0.875rem;
  padding: 0.625rem 1.25rem;
  border-radius: var(--radius-md);
  font-weight: 600;
  text-transform: none;
  letter-spacing: 0.025em;
  box-shadow: var(--shadow-md);
  transition: all var(--transition-normal);
}

.nav-links .btn:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.menu-icons {
  display: none;
  z-index: 100;
  font-size: 1.5rem;
  position: relative;
  color: var(--text-primary);
}

.menu-open,
.menu-close {
  position: absolute;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: var(--radius-md);
  transition: all var(--transition-normal);
}

.menu-open:hover,
.menu-close:hover {
  background: rgba(0, 0, 0, 0.1);
}

@media (max-width: 1024px) {
  nav {
    padding: 1rem 1.5rem;
  }

  .nav-links {
    gap: 1.5rem;
  }
}

@media (max-width: 768px) {
  .nav-links {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(255, 255, 255, 0.95);
    backdrop-filter: blur(20px);
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    transform: translateX(100%);
    visibility: hidden;
    z-index: 999;
    transition: all var(--transition-normal);
    padding: 2rem;
  }

  .nav-active .nav-links {
    transform: translateX(0);
    visibility: visible;
  }

  .menu-icons {
    display: block;
    position: absolute;
    top: 50%;
    right: 2rem;
    transform: translateY(-50%);
  }

  .nav-links a {
    font-size: 1.1rem;
    padding: 1rem 2rem;
    width: 200px;
    text-align: center;
  }

  .nav-links .btn {
    width: 200px;
    padding: 1rem 2rem;
  }
}
